@import url(https://fonts.googleapis.com/css2?family=Yellowtail&display=swap);
@import url(https://fonts.googleapis.com/css2?family=BioRhyme:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Yatra+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  text-align: center;
  margin: 0px;
  padding: 0px;

  /*FUENTES*/
  font-family: 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  -moz-osx-font-smoothing: antialiased;
}
#header {
  height: 120px;
  width: 100%;

  border-top: 5px solid #61dafb;
}

.article-content{
  font-family: 'Yatra One', cursive;
  font-size: 20px;
  text-align: justify;
  white-space: pre-wrap;
    word-spacing: 0.25em
}
.center{
  width: 75%;
  margin: 0px auto;
}

.clearfix{
  clear:both;
  float:none;
}

.empty{
  display: none;
}

#logo {
  width: 30%;
  font-size: 30px;
  font: "Droid Sans", sans-serif;
  float: left;
  margin-top: 35px;
  -webkit-animation-name: nace;
          animation-name: nace;
  -webkit-animation-duration: 8s;
          animation-duration: 8s;
}

#logo img {
  display: block;
  float: left;
  height: 60px;
  margin-top: -9px;
  margin-right: -10px;
  -webkit-animation: girar infinite  10s linear ;
          animation: girar infinite  10s linear ;
  -webkit-animation-delay: 6s;
          animation-delay: 6s;
}

@-webkit-keyframes nace{
  0% {
      -webkit-transform: scale(0, 0);
              transform: scale(0, 0);
  }

  100% {
      -webkit-transform: scale(1, 1);
              transform: scale(1, 1);
  }
}

@keyframes nace{
  0% {
      -webkit-transform: scale(0, 0);
              transform: scale(0, 0);
  }

  100% {
      -webkit-transform: scale(1, 1);
              transform: scale(1, 1);
  }
}
@-webkit-keyframes girar {
  from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
  }
  to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
  }
}
@keyframes girar {
  from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
  }
  to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
  }
}

#brand {
  display: block;
  margin: 0px;
  float: left;
}

#menu{
  width: 70%;
  float:right;
  font-size: 18px;
}

#menu ul{
  line-height: 85px;
  width: 60%;
  float:right;
}

#menu ul li{
  display: inline-block;
  list-style: none;
  height: 46px;
  margin-left: 15px;
  margin-right: 15px;
}

#menu a{
  text-decoration: none;
  color: #444;
  transition: 300ms all;
  font:  "Droid Sans", sans-serif;
}


#menu a:hover{
  color: #3cb2d3;
}
.favorita {
  background-color: green;
  color: aliceblue;
}
.active {
  border-bottom: 2px solid #3cb2d3;
}
#slider{
  width: 100%;
  height: 350px;
  /*line-height: 320px;*/
  color: white;
  text-shadow: 0px 0px 5px #444;

  /*DEGRADADO CSS*/
  background: rgb(43,156,180);
  background: linear-gradient(352deg, rgba(43,156,180,1) 6%, rgba(27,72,98,0.8883928571428571) 33%, rgba(66,212,242,1) 100%);
}

#slider h1{
  margin-top: 0.2px;
  padding: 0px;
  padding-top: 130px;
  text-align: center;
  font-family: 'Yellowtail', cursive;
}

.btn-white{
  display: block;
  background: white;
  color:rgb(88, 88, 88);
  padding: 10px;
  width: 100px;
  height: 35px;
  margin: 0px auto;
  font-size: 18px;
  text-transform: uppercase;
  text-shadow: none;
  text-decoration: none;
  line-height: 35px;
  box-shadow: 0px 0px 5px rgb(88, 88, 88);
  margin-top: 40px;
  border-radius: 4px;
  transition: 300ms all;
}

.btn-white:hover{
  background: #444;
  color:white;
}

.btn-liquid {
	display: inline-block;
	position: relative;
	width: 240px;
	height: 60px;

	border-radius: 27px;

	color: #fff;
	font: 700 14px/60px "Droid Sans", sans-serif;
	letter-spacing: 0.05em;
	text-align: center;
	text-decoration: none;
	text-transform: uppercase;
}

.btn-liquid .inner {
	position: relative;

	z-index: 2;
}

.btn-liquid canvas {
	position: absolute;
	top: -50px;
	right: -50px;
	bottom: -50px;
	left: -50px;

	z-index: 1;
}

@-webkit-keyframes flip{
  0% {
      -webkit-transform: scaleY(1.5) translateY(50px);
              transform: scaleY(1.5) translateY(50px);
  }

  100% {
      -webkit-transform: scaleY(1);
              transform: scaleY(1);
  }
}

@keyframes flip{
  0% {
      -webkit-transform: scaleY(1.5) translateY(50px);
              transform: scaleY(1.5) translateY(50px);
  }

  100% {
      -webkit-transform: scaleY(1);
              transform: scaleY(1);
  }
}

#slider.slider-small{
  height: 150px;
-webkit-animation-name: flip;
        animation-name: flip;
-webkit-animation-duration: 4s;
        animation-duration: 4s;
}

#slider.slider-small h1{
  margin-top: 0.2px;
  padding: 0px;
  padding-top: 53px;
}

#content{
  width: 70%;
  float:left;
  min-height: 650px;
  margin-right: 20px;
}

.sidebar{
  width: 25%;
  float:left;
}

.sidebar-item{
  background: #f7f7f7;
  padding: 20px;
  margin-top: 30px;
}

.sidebar-item h3{
  text-transform: uppercase;
  font-family: 'BioRhyme', serif;
  font-size: 18px;
  margin:0px;
  padding-bottom: 10px;
  margin-bottom: 10px;
  border-bottom: 3px solid #eee;
}

.sidebar-item .btn{
  margin: 0px auto;
  margin-top: 10px;
}

.btn{
 display: block;
 background: black;
 color: white;
 padding: 15px; 
 font-weight: bold;
 text-transform: uppercase;
 font-size: 14px;
 text-decoration: none;
 max-width: 120px;
 transition: 300ms all;
 border: none;
 cursor:pointer;
}

.btn-success{
  background: rgb(23, 150, 19);
}

.btn-success:hover{
  background:black;
}

.btn-danger{
  background: rgb(221, 12, 12);
  float: left;
  margin-right: 25px;
}

.btn-warning{
  background: rgb(247, 145, 13);
  float: left;
}

.mid-form{
  width: 50%;
  margin: 0px auto;
}

.mid-form label{
  display: block;
  float:left;
  text-align: left;
  margin-top: 10px;
  margin-bottom: 5px;
  font-size: 17px;
}


input[type="submit"]{
margin-top: 15px;
}

input[type="text"],
input[type="file"],
textarea{
  width: 100%;
  height: 6vh;
  white-space: pre-line;
  border: 1px solid #ccc;
  padding: 3px;
  margin-bottom: 5px;
  transition: 300ms all;
}

input[type="text"]:focus,
textarea:focus{
  box-shadow: 0px 0px 5px #eee inset;
}

.thumb{
  width: 200px;
  float: left;
  display: block;
}

textarea{
  width: 100%;
  height: 50vh;
  white-space: pre-line;
  min-width: 100%;
  max-width: 100%;
  min-height: 150px;
  max-height: 300px;
}

.radibuttons{
  margin-top: 10px;
  margin-bottom: 10px;
  float:left;
}


#footer{
  background: #f7f7f7;
  width: 100%;
  height: 70px;
  line-height: 70px;
  color: #444;
}

#footer p {
  margin: 0px;
  padding: 0px;
}

.article-item{
  width: 100%;
  border-bottom: 1px solid #eee;
  padding-bottom: 20px;
  padding-top: 20px;
  text-align: left;
}

.article-item .image-wrap {
  width: 130px;
  height: 130px;
  overflow:hidden;
  float:left;
  margin-right: 15px;
  border: 1px solid #ccc;
}

.article-item .image-wrap img{
  height: 100%;
  text-align: center;
}

.article-item h2{
  display: block;
  width:100%;
  padding: 0px;
  margin: 0px;
  margin-bottom: 7px;
  font-family: 'Yellowtail', cursive;
}

.article-item .date{
  display: block;
  width: 100%;
  color: rgb(122, 122, 122);
}

.article-item a{
  display: block;
  color: #444;
  text-decoration: none;
  margin-top: 10px;
}

.article-item a:hover{
  text-decoration: underline;
}

.subheader{
  font-size: 38px;
  border-bottom: 1px solid #eee;
  padding-bottom: 10px;
  font-family: 'BioRhyme', serif;
}

.article-detail .image-wrap{
  float:none;
  width:100%;
  margin-top: 40px;
  margin-bottom: 50px;

}

.article-detail .image-wrap img{
  max-height: 400px;
  width: 75%;
  text-align: center;
}
.article-detail .subheader{
  margin-top: 15px;
  margin-bottom: 0px;
  border:none;
}

.cover-article {
  background: url(/mariouaeh/nota-libre/static/media/hoja2.66a2a3cb.jpg);
	background-attachment: fixed;
}

/*RESPONSIVE*/

/*Desde la anchura 963px hasta la anchura 1488px de pantalla*/
@media (max-width: 1488px){
  /*Se ejecutarán estos estilos*/
  .center{
      width: 85%;
  }
}

/*Desde la anchura 0px hasta la anchura 963px de pantalla*/
@media (max-width: 1314px){
  .center{
      width: 95%;
  }

  #menu ul{
      width: auto;
  }
}

@media (max-width: 797px){
  #logo{
      float:none;
      width: 240px;
      margin: 0px auto;
      margin-top: 20px;
  }

  #menu,
  #menu ul{
     clear:both;
     float:none;
     width: 100%;
     margin: 0px;
     padding: 0px;
     line-height: 43px;
  }

  #content{
      float:none;
      width: 100%;
  }

.formuser, .sidebar{
      float:none;
      width: 60%;
      margin: 0px auto;
      margin-bottom: 50px;
      margin-top: 30px;
  }

  #slider h1{
      padding-top: 115px;
      font-size: 25px;
  }

  .mid-form{
      width: 70%;
  }
}

@media (max-width: 497px){
  #header{
      min-height: 130px;
      overflow:hidden;
  }

  #menu,
  #menu ul{
      line-height: 50px;

  }

  #menu ul li{
      margin-left: 7px;
      margin-right: 7px;
  }

  #slider{
      height: 180px;
  }

  #slider h1{
      font-size: 19px;
      padding-top: 30px;
  }

  .empty {
    display: none;
  }

  #slider .btn-white{
      margin-top: 20px;
  }

  #slider.slider-small{
      height: 100px;
  }

  #slider.slider-small h1{
     padding-top: 35px;
  }

}
.pixelart-to-css {

  height: 400px;
  width: 400px;
  animation: x 1.2s infinite;
  -webkit-animation: x 1.2s infinite;
  -moz-animation: x 1.2s infinite;
  -o-animation: x 1.2s infinite;
}

.text {
    font-weight: 600;
    font-size: 8em;
    will-change: opacity;
  }

  .container {
    display: flex;
    align-items: center;
    height: 100%;
    justify-content: center;
  }


  .item {
    position: relative;
    width: 300px;
    height: 100px;
    pointer-events: auto;
    -webkit-transform-origin: 50% 50% 0px;
            transform-origin: 50% 50% 0px;
    padding-left: 32px;
    padding-right: 32px;
    box-sizing: border-box;
    display: grid;
    align-items: center;
    text-align: center;
    border-radius: 5px;
    box-shadow: 0px 10px 10px -5px rgba(0, 0, 0, 0.2);
    -webkit-user-select: none;
    -ms-user-select: none;
        user-select: none;
  }
  
  .fg {
    cursor: -webkit-grab;
    background-color: #272727;
    color: rgba(255, 255, 255, 0.8);
    position: absolute;
    height: 100%;
    width: 100%;
    display: grid;
    align-items: center;
    text-align: center;
    border-radius: 5px;
    box-shadow: 0px 10px 30px -5px rgba(0, 0, 0, 0.2);
    font-size: 3em;
  
    font-weight: 600;
    transition: box-shadow 0.75s;
  }
  
  .fg:active {
    cursor: -webkit-grabbing;
    box-shadow: 0px 15px 30px -5px rgba(0, 0, 0, 0.4);
  }
  
  .fg > * {
    pointer-events: none;
  }
  
  .av {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    background-color: white;
  }
  
  .container {
    display: flex;
    align-items: center;
    height: 100%;
    justify-content: center;
  }
  
